<template>
  <div class="addressManagement">
    <div class="addressManagementHeader">
      <div class="headerContent">
        <van-icon name="arrow-left" @click="$router.go(-1)" />
        <span class="title">{{ $t("index.text39") }}</span>

        <div class="right jcc" @click="operation(0)">
          <van-icon name="plus" />
          <span>{{ $t("index.text40") }}</span>
        </div>
      </div>
    </div>
    <div class="slot"></div>

    <div class="addressManagementMain">
      <div class="receivingAddress" v-for="item in addressList" :key="item.id">
        <div>
          <div class="userInfo  jcsb">
            <span>{{ item.name }}</span>
            <span>{{ item.phone }}</span>
          </div>

          <div class="userAddress mt2r">
            {{ item.address }}
          </div>
        </div>

        <div class="line"></div>

        <div class="edit jcsb">
          <div class="editItem">
            <span @click="defaultAddress(item)" v-show="item.is_default == 0">{{
              $t("index.text41")
            }}</span>

            <span style="color: #D12C21;" v-show="item.is_default == 1">{{
              $t("index.text42")
            }}</span>
            <van-icon
              name="success"
              color="#D12C21"
              size="1.5rem"
              v-show="item.is_default == 1"
            />
          </div>

          <div class="editItem" @click="operation(1, item)">
            <i class="el-icon-edit-outline"></i>
            <span>{{ $t("index.text43") }}</span>
          </div>

          <div class="editItem" @click="deleteAddress(item)">
            <i class="el-icon-delete"></i>
            <span>{{ $t("index.text44") }}</span>
          </div>
        </div>
      </div>
    </div>

    <van-popup
      v-model="showAddAddressPopup"
      position="bottom"
      class="showAddAddressPopup"
    >
      <!-- <van-address-edit
        :address-info="addressInfo"
        :show-area="false"
        @save="onSave"
        :save-button-text="$t('index.text10')"
        ref="addressEdit"
      /> -->

      <van-form @submit="onSubmit">
        <van-field
          v-model="addressInfo.name"
          :name="$t('index.text50')"
          :label="$t('index.text50')"
          :placeholder="$t('index.text51')"
          :rules="[{ required: true }]"
        />
        <van-field
          v-model="addressInfo.tel"
          :name="$t('index.text52')"
          :label="$t('index.text52')"
          :placeholder="$t('index.text53')"
          :rules="[{ required: true }]"
        />
        <van-field
          v-model="addressInfo.addressDetail"
          autosize
          :name="$t('index.text54')"
          :label="$t('index.text54')"
          type="textarea"
          :placeholder="$t('index.text55')"
          :rules="[{ required: true }]"
        />
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">{{
            $t("index.text10")
          }}</van-button>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      addressList: [],
      type: 0,
      showAddAddressPopup: false,
      selectQuantity: 1,
      addressInfo: {
        name: "", //姓名
        tel: "", //电话
        addressDetail: "",
        is_default: 0 //地址
      }
    };
  },
  async activated() {
    this.getAddressList();
  },
  methods: {
    getAddressList() {
      this.$api
        .get("address/index", {
          page: 1,
          limit: 99999
        })
        .then(res => {
          this.addressList = res.data.list;
        });
    },
    // 0 新增 | 1 修改
    operation(type, item) {
      this.type = type;
      if (type == 1) {
        this.addressInfo.id = item.id;
        this.addressInfo.name = item.name;
        this.addressInfo.tel = item.phone;
        this.addressInfo.addressDetail = item.address;
        this.addressInfo.is_default = item.is_default;
      } else {
        this.empty();
      }
      this.showAddAddressPopup = true;
    },
    onSubmit(v) {
      if (this.type == 0) {
        this.$api
          .post("address/add", {
            name: this.addressInfo.name,
            phone: this.addressInfo.tel,
            address: this.addressInfo.addressDetail,
            is_default: 0,
            area: "86"
          })
          .then(res => {
            if (res.code == 200) {
              this.$toast(this.$t("index.text45"));
              this.getAddressList();
              this.empty();
              this.showAddAddressPopup = false;
            }
          });
      } else {
        this.$api
          .post("address/edit", {
            id: this.addressInfo.id,
            name: this.addressInfo.name,
            phone: this.addressInfo.tel,
            address: this.addressInfo.addressDetail,
            is_default: 0
          })
          .then(res => {
            if (res.code == 200) {
              this.$toast(this.$t("index.text46"));
              this.getAddressList();
              this.empty();
              this.showAddAddressPopup = false;
            }
          });
      }
    },
    deleteAddress(item) {
      this.$dialog
        .confirm({
          title: this.$t("index.text47")
        })
        .then(() => {
          this.$api
            .get("address/del", {
              id: item.id
            })
            .then(res => {
              this.$toast(this.$t("index.text48"));
              this.getAddressList();
            });
        })
        .catch(() => {
          // on cancel
        });
    },
    defaultAddress(item) {
      this.$api
        .post("address/edit", {
          id: item.id,
          is_default: 1
        })
        .then(res => {
          this.$toast(this.$t("index.text49"));
          this.getAddressList();
        });
    },
    empty() {
      this.addressInfo.id = undefined;
      this.addressInfo.name = undefined;
      this.addressInfo.tel = undefined;
      this.addressInfo.addressDetail = undefined;
      this.addressInfo.is_default = 0;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.addressManagement {
  height: 100%;
  overflow: scroll;
}
.addressManagementHeader {
  padding: 0 2rem;
  background: #d12c21;
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  z-index: 99;

  .headerContent {
    display: flex;
    align-items: center;
    position: relative;
    height: 5rem;
  }
  .van-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  .title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1.6rem;
  }

  .right {
    position: absolute;
    right: 0;
    color: #fff;
    font-size: 1.6rem;
    .van-icon {
      margin-top: 0.2rem;
      font-size: 1.4rem;
    }
    span {
      margin-left: 0.3rem;
      color: #fff;
    }
  }
}
.addressManagementMain {
  padding: 0 1.6rem 1.6rem;
  .receivingAddress {
    margin-top: 2rem;
    background: rgba(245, 201, 109, 0.3);
    border-radius: 0.8rem;
    padding: 1.5rem;
    .line {
      margin: 1.2rem 0;
      height: 0.1rem;
      background: #999999;
    }
    .userInfo {
      span {
        font-weight: bold;
      }
    }
    .userAddress {
      line-height: 2.5rem;
    }
    .emptyAddress {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      img {
        width: 5rem;
        height: 5rem;
      }
      div {
        font-weight: bold;
      }
    }
  }
}

.showAddAddressPopup {
  border-radius: 2rem 2rem 0 0;
  padding: 1.6rem;
  /deep/ .van-button {
    height: 5rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    border: 0;
    margin: 0;
    .van-button__text {
      color: #fff;
    }
    .van-address-edit__buttons {
      padding: 0;
    }
  }
}
</style>



